<template>
    <div>
        <el-form
                :rules="rules"
                :model="loginForm"
                v-loading="loading"
                element-loading-text="正在登录"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                ref="ruleForm"
                class="loginContainer">
            <h3 class="loginTitle">系统登录</h3>
            <el-form-item prop="username">
                <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input type="password"
                          v-model="loginForm.password"
                          auto-complete="off" placeholder="请输入密码" ></el-input>
            </el-form-item>
            <el-checkbox v-model="checked" class="loginRememberme">记住我</el-checkbox>
            <el-button type="primary" style="width: 100%" @click="submitLogin('ruleForm')" @keydown.enter.native="submitLogin">登录</el-button>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                loading: false,
                loginForm: {
                    username: 'admin',
                    password: '123'
                },
                checked: true,
                rules: {
                    username: [
                        { required: true, message: '请输入用户名', trigger: 'blur'}
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            submitLogin(formName) {
                this.$refs[formName].validate((valid) => {
                   if (valid) {
                       this.loading = true;
                       this.postKeyValueRequest('/doLogin', this.loginForm).then(resp=>{
                           this.loading = false;
                           if (resp) {
                               window.sessionStorage.setItem('user', JSON.stringify(resp.object));
                               // 在这里进行用户未登录时在地址栏中请求的页面地址处理
                               let path = this.$route.query.redirect;
                               this.$router.replace((path == '/' || path == undefined) ? '/home' : path);
                           }
                       })
                   } else {
                       this.$message.error('请输入用户名或密码');
                       return false;
                   }
                });
            }
        }
    }
</script>

<style >
    .loginContainer {
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;     /*上下180px, 左右自适应*/
        width: 350px;
        padding: 10px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }
    .loginTitle {
        margin: 10px auto 20px auto;
        text-align: center;
        color: #505458;
    }
    .loginRememberme {
        margin: 0px auto 15px auto;
    }
</style>