import axios from "axios";
import {Message} from "element-ui";
import en from "element-ui/src/locale/lang/en";
import router from "../router";

axios.interceptors.response.use(success => {
    /*
    *  当服务器状态码为200时，会进入成功回调函数
    * 但此时响应码为200并不确定请求是否真的成功，可能存在业务上的错误
    * 如：用户操作失败，权限不足
    * 应当判断状态码是否存在业务上的错误
    * */

    // success.data.status 是服务端返回数据中携带的状态码，表示业务上的操作错误
    // success.status 这是服务器响应的状态码
    if (success.status && success.status == 200 && success.data.status == 500) {
        Message.error({message: success.data.msg});
        console.log(success.data.msg);
        return;
    }

    if (success.data.msg) {
        Message.success({message: success.data.msg});
    }
    return success.data;
}, error => {
    if (error.response.status == 504 || error.response.status == 404) {
        Message.error({message: '服务器被吃了( ╯□╰ )'});
    } else if (error.response.status == 403) {
        Message.error({message: '权限不足，请联系管理员'});
    } else if (error.response.status == 401) {
        Message.error({message: '尚未登录，请登录'});
        // 跳转登录页面
        router.replace("/");
    } else {
        if (error.response.data.msg) {
            Message.error({message: error.response.data.msg});
        } else {
            Message.error({message: '未知错误'});
        }
    }
    return;
})

// 用于作路径拼接统一前缀
let base = '';
/**
 *
 * @param url   请求的url地址
 * @param params    需要携带的参数
 * @returns {AxiosPromise}  拼接好的key=value 形式的参数
 */
export const postKeyValueRequest = (url, params) => {
    return axios({
        method: 'post',
        url: `${base}${url}`,
        data: params,
        transformRequest: [function (data) {
            let ret = '';
            for (let i in data) {
                ret += encodeURIComponent(i) + '=' + encodeURIComponent(data[i]) + '&';
            }
            return ret;
        }],
        headers: {
            'Content-Type':'application/x-www-form-urlencoded'
        }
    });
}

export const postRequest = (url, params) => {
    return axios({
       method: 'post',
       url: `${base}${url}`,
       data: params
    });
}

export const putRequest = (url, params) => {
    return axios({
        method: 'put',
        url: `${base}${url}`,
        data: params
    });
}

export const getRequest = (url, params) => {
    return axios({
        method: 'get',
        url: `${base}${url}`,
        data: params
    });
}

export const deleteRequest = (url, params) => {
    return axios({
        method: 'delete',
        url: `${base}${url}`,
        data: params
    });
}
